<template>
  <div>
    <div>
      <van-nav-bar
        title="我的关注"
        left-text="返回"
        right-text="按钮"
        left-arrow
        @click-left="onClickLeft"
      />
    </div>

    <!-- ======关注详情 -->
    <div class="guan_count_da">
      <div v-for="(item,i) in guanzhu" :key="i" class="guan_count">
        <div class="guan_image_da">
          <div class="guan_img">
            <img :src="item.avatar" />
          </div>
          <div class="guan_name_da">
            <div class="guan_name">{{item.teacher_name}}</div>
            <div class="guan_level">
              {{item.level_name}}
              <div class="guan_introduction">{{item.introduction}}</div>
            </div>
            <div class="quguan" @click="cancelClick">取消关注</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      guanzhu: [],
      collectID: Number
    };
  },
  methods: {
    onClickLeft() {
      this.$router.push({
        name: "MyMine"
      });
    },
    cancelClick() {
      this.$http.put(`/api/app/collect/cancel/${this.collectID}/2`).then(res => {
          //   console.log(res.data);
          this.$http.get("/api/app/collect?page=1&limit=10&type=2&").then(res => {
              // console.log(res.data.data.list)
              res.data.data.list.forEach(ele => {
                this.collectID = ele.collect_id;
                // console.log(this.collectID)
              });
              this.guanzhu = res.data.data.list;
            });
        });
    }
  },
  mounted() {
    this.$http.get("/api/app/collect?page=1&limit=10&type=2&").then(res => {
      // console.log(res.data.data.list)
      res.data.data.list.forEach(ele => {
        this.collectID = ele.collect_id;
        // console.log(this.collectID)
      });
      this.guanzhu = res.data.data.list;
    });
  }
};
</script>

<style scoped>
.guan_count_da {
  width: 100%;
  /* height: 10rem; */
  background-color: whitesmoke;
}
.guan_count {
  width: 100%;
  /* display: inline-flex; */
}
.guan_image_da {
  width: 100%;
  /* background-color: yellow; */
  display: inline-flex;
  margin-top: 1rem;
}
.guan_name_da {
  width: 100%;
  height: 5rem;
  background-color: white;
  display: inline-flex;
  margin-top: 1rem;
}
.guan_introduction {
  width: 14rem;
  /* background-color: hotpink; */
  font-size: 12px;
  color: grey;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  margin-left: -3rem;
}
.guan_img {
  width: 3rem;
  height: 3rem;
  margin-top: 1rem;
}
.guan_img img {
  width: 100%;
  height: 3rem;
}
.guan_name {
  color: gray;
  margin-left: 1rem;
}
.guan_level {
  color: orangered;
  margin-left: 0.5rem;
  font-size: 12px;
  margin-top: 0.5rem;
}
.quguan {
  width: 5rem;
  height: 2rem;
  background-color: aqua;
  border-radius: 10rem;
  color: orangered;
  background-color: rgba(255, 123, 0, 0.11);
  text-align: center;
  line-height: 2rem;
  /* float: right; */
  z-index: 999;
  margin-top: 1rem;
  font-size: 12px;
  margin-left: 0.5rem;
}
</style>